.videos-container {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: flex-start;
    min-height: 100vh;
    background: linear-gradient(to bottom right, #f0f4f8, #87CEFA, #4682B4);
    padding: 40px;
    color: #fff;
    font-family: Arial, sans-serif;
    width: 100%; /* √âtend le conteneur √† la largeur de la page */
    position: relative; /* N√©cessaire pour les pseudo-√©l√©ments */
    overflow: hidden; /* Pour √©viter les d√©bordements */
  }
  
  /* Decorative Background */
  .videos-container::before {
    content: "";
    position: absolute;
    top: -10%;
    left: -10%;
    width: 400px;
    height: 400px;
    background: rgba(255, 255, 255, 0.15);
    border-radius: 50%;
    z-index: 0;
    animation: floating 6s ease-in-out infinite;
  }
  
  .videos-container::after {
    content: "";
    position: absolute;
    bottom: -10%;
    right: -10%;
    width: 300px;
    height: 300px;
    background: rgba(255, 255, 255, 0.15);
    border-radius: 50%;
    z-index: 0;
    animation: floating 8s ease-in-out infinite reverse;
  }
  

@keyframes floating {
    0%, 100% {
        transform: translateY(0px);
    }

    50% {
        transform: translateY(20px);
    }
}

.videos-container h2 {
    color: #2C3E50;
}

.videos-container p {
    color: #687f96;
}

.topic-buttons {
    display: flex;
    justify-content: center;
    flex-wrap: wrap;
    margin: 20px 0;
    gap: 20px;
    width: 70%;
}

.topic-image {
    width: auto;
    height: 300px;
    object-fit: cover;
    border-radius: 8px;
    cursor: pointer;
    box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
    transition: transform 0.3s ease, box-shadow 0.3s ease;
}

    .topic-image:hover {
        transform: scale(1.05);
        box-shadow: 0 6px 10px rgba(0, 0, 0, 0.15);
    }

.topic-title {
    font-size: 18px;
    color: #2C3E50;
    text-align: center;
    margin-top: 5px;
}

.videos-list {
    margin-top: 20px;
    width: 70%;
}

    .videos-list ul {
        list-style-type: none;
        padding: 0;
    }

    .videos-list li {
        background-color: #f1f1f1;
        margin: 30px auto; /* ◊û◊®◊õ◊ñ ◊ê◊™ ◊î◊ß◊ï◊§◊°◊î */
        padding: 30px;
        width: 100%; /* ◊ô◊û◊ú◊ê ◊ê◊™ ◊õ◊ú ◊î◊®◊ï◊ó◊ë ◊©◊ú ◊î◊ß◊ï◊†◊ò◊ô◊ô◊†◊® */
        border-radius: 10px;
        text-align: center;
        box-shadow: 0 6px 12px rgba(0, 0, 0, 0.2); /* ◊¶◊ú ◊ú◊î◊ì◊í◊©◊î */
    }




    .videos-list iframe {
        width: 100%; /* ◊î◊°◊®◊ò◊ï◊ü ◊ô◊û◊ú◊ê ◊ê◊™ ◊õ◊ú ◊î◊®◊ï◊ó◊ë ◊©◊ú ◊î◊ß◊ï◊§◊°◊î */
        max-width: 1200px; /* ◊î◊í◊ë◊ú◊î ◊ú◊í◊ï◊ì◊ú ◊û◊°◊ö ◊®◊ó◊ë */
        height: 70vh; /* ◊í◊ï◊ë◊î ◊î◊°◊®◊ò◊ï◊ü ◊õ-70% ◊û◊í◊ï◊ë◊î ◊î◊û◊°◊ö */
        border: none;
        border-radius: 10px;
        margin: 20px auto; /* ◊û◊®◊õ◊ñ ◊ê◊™ ◊î◊°◊®◊ò◊ï◊ü */
        display: block;
    }



.back-button {
    align-self: flex-start; /* ◊ô◊û◊ï◊ß◊ù ◊ë◊¶◊ì ◊î◊î◊™◊ó◊ú◊™◊ô ◊©◊ú ◊î◊©◊§◊î */
    margin-top: 40px;
    padding: 10px 20px;
    background-color: #4682B4;
    color: #fff;
    border: none;
    border-radius: 5px;
    cursor: pointer;
}

    .back-button:hover {
        background-color: #5A9BD4;
    }

